<template>
  <div class="jsskill">
    <h2>JS知识</h2>
    <p>递归</p>
    <div class="code jscode">
      <div class="code-notes">
        定义:函数自己调用自己,调用函数要去执行函数的函数体<br />
        注意:一定要有结束的时候
      </div>

      var i = 0;<br />
      function sb(){<br />
      <div class="space-1 ">i++;</div>
       <div class="space-1 ">console.log("我是一个随便的函数");</div>
       <div class="space-1 ">if(i &lt; 3){</div>
       <div class="space-2 ">sb();</div>
       <div class="space-1 ">}</div>
       <div class="space-1 ">console.log("呵呵"+i);</div>
      
      }<br>
      sb();
    </div>
    <p>闭包</p>
    <div class="code jscode">
      <div class="code-notes">
      声明在一个函数内部,可以访问函数内部的局部变量的函数,叫做闭包.
      </div>
      <div class="space-0 ">function test(){</div>
        <div class="space-1 "> var num = 10;<span class="code-notes">//局部变量</span></div>
        <div class="space-1 ">console.log("我是test函数");</div>
        <div class="space-1 ">function sb(){</div>
        <div class="space-2 ">console.log(num);</div>
        <div class="space-1 ">}</div>
        <div class="space-1 ">return sb; <span class="code-notes">//这句话是返回这个sb函数本身.</span
      ></div>
        <div class="space-0 ">} </div>
        <div class="space-0 ">var fn = test();<span class="code-notes"
        >//调用test函数,有返回值,返回值是sb这个函数.
        那接受的fn就相当于这个sb函数了.</span
      ></div>
      fn(); <span class="code-notes">//调用fn,相当于调用sb函数.</span> 
    </div>
  </div>
</template>

<script>
export default {};
</script>

