<template>
  <div
    id="container"
    :class="{ night: isNight }"
    v-title
    data-title="张凡个人网站"
  >
    <el-container class="index-container">
      <el-header class="index-header" style="height: 52px">
        <div class="tit">
          <a href="http://zhangfine.top">张凡个人网站</a>
        </div>
        <i
          @click="mobileMenuShow = !mobileMenuShow"
          class="iconfont icon-Menu"
        ></i>

        <el-switch
          class="theme"
          @change="changeTheme"
          v-model="isNight"
          active-color="#00a08a"
          inactive-color="#666"
        ></el-switch>
      </el-header>
      <div class="nav">
        <div class="user-tag"></div>
        <div class="center-card">
          <div class="tag-list tab-nav">
            <div
              class="tag-item"
              :class="{ active: isActive == 0 }"
              @click="tabActive(0)"
            >
              <a href="javascript:void(0)">生活</a>
            </div>
            <div
              class="tag-item"
              :class="{ active: isActive == 1 }"
              @click="tabActive(1)"
            >
              <a href="javascript:void(0)">技能</a>
            </div>
            <div
              class="tag-item"
              :class="isActive == 2 ? 'active' : ''"
              @click="tabActive(2)"
            >
              <a href="javascript:void(0)">案例</a>
            </div>
            <div
              class="tag-item"
              :class="isActive == 3 ? 'active' : ''"
              @click="tabActive(3)"
            >
              <a href="javascript:void(0)">其他</a>
            </div>
          </div>
          <div class="tab-content">
            <div class="left-btn tab-btn">&lt;</div>
            <div class="article-list">
              <router-link
                :to="item.url"
                class="article-item"
                v-for="item in currentTabData"
                :key="item.url"
              >
                {{ item.title }}
              </router-link>
            </div>

            <div class="right-btn tab-btn">&gt;</div>
          </div>
        </div>
      </div>
      <el-container class="index-main">
        <el-main class="main-center">
          <el-scrollbar style="height: 100%">
            <router-view></router-view>
          </el-scrollbar>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      // tab切换栏
      isActive: 0,
      tabData: {
        live: [
          { title: "十大恐怖电影", url: "horrormovie" },
          { title: "网站推荐", url: "websites" },
          { title: "好物推荐", url: "wishlist" },
          { title: "iocn图标", url: "ico" },
        ],
        skill: [
          { title: "axios", url: "axios" },
          { title: "ES6", url: "es6" },
          { title: "H5C3", url: "h5c3" },
          { title: "js知识", url: "jsskill" },
          { title: "小程序", url: "miniprogram" },
          { title: "moment使用", url: "moment" },
          { title: "router使用", url: "routeruse" },
          { title: "滚动条", url: "scrollbar" },
          { title: "vue传值", url: "vuechuanzhi" },
          { title: "VueX", url: "vuex" },
        ],
        example: [
          { title: "背景显示", url: "bgshow" },
          { title: "图片显示", url: "imgshow" },
          { title: "less使用", url: "varless" },
        ],
        other: [
          { title: "iTtool", url: "ittool" },
          { title: "mega", url: "mega" },
          { title: "首页", url: "index" },
        ],
      },
      currentTabData: [],
      //是否深色主题
      isNight: false,
      // 浅色背景颜色
      dayBg: "#f9f9f9",
      // 深色背景颜色
      nightBg: "#232324",
      //当前背景颜色
      varBg: "#f9f9f9",
      //板块背景颜色
      mainBg: "#333333",

      //列表是否只保持一个子菜单的展开
      isMenuOpened: false,
      //当前窗口大小
      screenWidth: document.body.clientWidth,
      mobileMenuShow: true,
    };
  },
  methods: {
    // tab切换栏
    tabActive(index) {
      this.isActive = index;
      if (index == 0) {
        this.currentTabData = this.tabData.live;
      } else if (index == 1) {
        this.currentTabData = this.tabData.skill;
      } else if (index == 2) {
        this.currentTabData = this.tabData.example;
      } else if (index == 3) {
        this.currentTabData = this.tabData.other;
      }
    },
    changeTheme() {
      if (this.isNight) {
        window.localStorage.setItem("theme", 1);
      } else {
        window.localStorage.setItem("theme", 0);
      }
    },
  },
  created() {
    this.currentTabData = this.tabData.live;

    if (window.localStorage.getItem("theme") == 1) {
      this.isNight = true;
    } else {
      this.isNight = false;
    }
  },
  mounted() {
    //监听窗口大小变化

    window.onresize = () => {
      this.screenWidth = document.body.clientWidth;

      if (this.screenWidth >= 992) {
        this.isMenuOpened = false;
        this.mobileMenuShow = true;
      } else if (this.screenWidth < 992) {
        this.isMenuOpened = true;
        //低于992时导航默认隐藏,但此处992与媒体查询的992存在区别,此处宽度不包含导航条,所以992附件可能出现导航消失但菜单按钮未出现
        this.mobileMenuShow = false;
      }
    };
  },
};
</script>

